import React, { Component } from 'react';
import './App.css';



class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      results: [],
      artistResults: [],
      searchWord: "",
      isSearching: true,
      clickedArtistId: null,
      albumArt: ""
    }
  }


  setSearchWord = (x) => {
    this.setState({ searchWord: x.target.value })
  }


  getInfo = () => {
    let api_url = `https://cors-anywhere.herokuapp.com/https://itunes.apple.com/search?term=${this.state.searchWord}&entity=musicArtist&media=music`;
    fetch(api_url)
      .then(res => res.json())
      .then(json => {
        this.setState({ results: json.results });
      })
      .catch(error => console.error(error));
  }


  getAlbumInfo = (res) => {
    let api_url = `https://cors-anywhere.herokuapp.com/https://itunes.apple.com/lookup?id=${res.target.id}&entity=album`;
    fetch(api_url)
      .then(res => res.json())
      .then(json => {
        this.setState({
          clickedArtistId: json.results.artistId,
          artistResults: json.results,
          isSearching: false,
          albumArt: json.results.artworkUrl60
        });
      })
  }


  backToSearch = () => {
    this.setState({ isSearching: true });
  }


  render() {

    const listMatches = this.state.results.map((artist) => <li id={artist.artistId} onClick={this.getAlbumInfo}>{artist.artistName}</li>);



    const listAlbums = this.state.artistResults.map((album) =>

      <li id={album.collectionId}>
        <div>
          <div class="columns">
            <div class="column album">
              <figure class="image is-64x64">
                <img src={album.artworkUrl60} alt="" />
              </figure>
            </div>
            <div class="column">
              <p>
                {album.collectionName}
              </p>
            </div>
          </div>
        </div>
      </li>

    );



    if (this.state.isSearching) {
      return (
        <div className="App">
          <div className="box is-primary">
            <div className="field">
              <p className="control has-icons-left has-icons-right">
                <input className="input is-medium is-primary is-rounded" type="text" onChange={this.setSearchWord} placeholder="Search artist..." />
                <span className="icon is-small is-left">
                  <i className="fas fa-search"></i>
                </span>
              </p>
            </div>
            <a onClick={this.getInfo} className="button is-primary">Search</a>
            <ul>{listMatches}</ul>
          </div>
        </div>
      )

    } else if (this.state.isSearching === false) {
      return (
        <div className="App">
          <div className="box">
            <a onClick={this.backToSearch} className="button is-primary">Back</a>
            <ul>{listAlbums}</ul>
          </div>
        </div>
      )
    }
  }
}

export default App;